        .bt-tabs {
            ul.bt-nav__ul {
                li.bt-nav__item {
                    .bt-nav__icon {
                        color: rgba(255, 255, 255, 0.5);
                        &.active {
                            color: #fff;
                        }
                    }
                }
            }
        }

        a {
            color: $t-color;
            text-decoration: none;
        }

        a:focus {
            color: $t-color;
            text-decoration: none;
        }
    }

    nav.bg-dark {
