                    background: #fff !important;
                }

                .n-name {
                }

                .n-username {
                }
                .n-items {
                    color: $t-color;
                    a,
                    a:hover,
                    a:focus {
                        // color: $t-color;
                        &.logout {
                            color: rgb(219, 54, 54);
                        }

                        &.l-item {
                            color: $t-color;
                        }
                    }
                }
                .n-b-items {
                }
            }
            .contents {
                background-color: $t-background;
                color: $t-color;
            }

            .closer {
            }

            .icon-bar {
                background-color: darken($t-color, 10%);
            }
        }
    }

    .nav-custom-container {
        border-bottom: 1px solid $t-border-color-nav;
