    /* ======================================= */

    svg.loader {
        path,
        rect {
            fill: #117dfc;
        }
    }

    /* ======================================= */
    .custom-card {
        background-color: $t-foreground !important;

        .u-name {
            color: $t-color;
        }

        .u-username {
            color: #999;
        }

        .text-banner {
            color: $t-foreground;
        }

        .card-content {
            .title {
                color: $t-color;
            }

            .time {
                color: #bdbdbd80;
            }

            .content {
                color: #aaaaaa;
            }

            .tags {
                color: #a5a5a580;
            }
        }
    }

    svg.action-icon {
        path,
        rect {
            fill: darken($t-color, $amount: 30%) !important;
        }
    }
    
    .custom-card2 {
        background-color: $t-foreground;

        .drop-btn {
            background-color: $t-foreground;
            color: $t-color;
        }
        .drop-btn.active,
        .drop-btn:focus {
            // background-color: $dropdown-active;
        }
        .drop-btn:hover {
            // background-color: $dropdown-hover;
        }

        .u-name {
            color: $t-color;
        }

        .u-username {
            color: #999;
        }

        .article-content {
            color: rgba(224, 224, 224, 1);
        }

        svg.action-icon.liked {
            path,
            rect {
                fill: rgb(240, 70, 70) !important;
            }
        }

        svg.action-icon.bookmarked {
            path,
            rect {
                fill: #2196f3 !important;
            }
        }
    }

    hr.light {
        background: #424242 !important;
    }
