    .settings-section {
        .profile-edit-btn {
            border: #191b1f;
        }

        .settings-nav.nav-pills {
            .nav-item {
                .nav-link {
                    color: $t-dull-color;
                    padding-left: 30px;
                    // .nav-btn.active {
                    //     background-color: $dropdown-active;
                    // }
                    &.nav-btn:hover {
                        background-color: darken($dropdown-hover, 2%);
                    }
                }

                .nav-link.active {
                    background-color: $t-foreground;
                    color: $t-color;
                    padding-left: 24px;
                    border-left: 6px solid $dropdown-active;
                }
            }
        }
    }
