    .tab-section .nav-tabs {
        // border-bottom: 1px solid $t-border-color-nav;
        border-bottom: unset;
        background-color: $t-foreground;
    }

    .tab-section .nav-tabs .nav-item {
        color: #b0b3b8;
        background-color: $t-foreground;
        // border-bottom: 1px solid #666;
    }

    .tab-section .nav-tabs .nav-item:hover {
        color: #d8d8d8;
        background-color: $dropdown-hover;
        // border-bottom: 1px solid #666;
    }

    .tab-section .nav-tabs .nav-item.active {
        color: $t-nav-pill-color;
        background-color: $t-foreground !important;
        border-bottom: 2px solid $t-nav-pill-color;
    }

    .tab-section .nav-pills .nav-item {
        color: #9c9c9c;
        background: $t-background;
        font-size: 18px;
    }

    .tab-section .nav-pills .nav-item.active {
        color: $t-nav-pill-color !important;
        font-weight: 500;
        background: $t-background;
        font-size: 18px;
    }

    .tab-section .nav-pills .nav-item:hover {
        color: $t-color;
        background: $t-background;
        font-size: 18px;
    }
