    .btn-info {
        background-color: #1a1c22;
        color: #f3f5f9;
        border-color: #bbb;
    }

    .btn-info:active,
    .btn-info:focus {
        background-color: #303238;
        border-color: #393b3b;
        color: #afafaf;
    }

    .btn-info:hover {
        background: #303542;
        color: #f3f5f9;
        border-color: #bbb;
    }
