    ::selection {
        color: darken($t-color, 10%) !important;
        background: lighten($t-foreground, 20%);
    }

    ::-webkit-input-placeholder {
        /* Edge */
        color: red;
    }

    :-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: red;
    }

    ::placeholder {
        color: red;
    }

    .ce-block,
    .ce-block__content,
    .ce-toolbar,
    .ce-toolbar__plus,
    .ce-conversion-toolbar,
    .ce-conversion-tool:hover,
    .ce-conversion-tool__icon,
    .ce-inline-tool,
    .ce-inline-toolbar,
    .ce-inline-toolbar:hover,
    .ce-inline-toolbar__dropdown:hover,
    .ce-toolbar__actions,
    .ce-toolbox__button,
    .ce-toolbar__settings-btn,
    .cdx-block,
    .cdx-input,
    .cdx-loader,
    .cdx-button,
    .cdx-notifies,  //settings
    .cdx-notify,
    .cdx-settings-button,
    .cdx-settings-button--active {
        background-color: $t-background !important;
        color: $t-color !important;

        *::selection {
            color: darken($t-color, 10%) !important;
            background: lighten($t-foreground, 20%);
        }
    }

    .ce-block.ce-block--selected {
        *,
        * > .ce-block__content,
        * > .ce-block__content > .cdx-block,
        * > .ce-block__content > .cdx-image {
            color: darken($t-color, 10%) !important;
            background: lighten($t-foreground, 20%) !important;
        }
    }

    .modal-content {
        background-color: $t-background;
        border: 1px solid #424242 !important;
        .modal-header {
            border-bottom: 1px solid #424242 !important;
        }

        .modal-footer {
            border-top: 1px solid #424242 !important;
        }

        .close {
            color: $t-color;
        }
    }

    .cs-dropdown-container {
        .cs-dropdown-menu {
            background-color: $t-foreground;
            border: 1px solid $dropdown-border;
            color: $dropdown-color;
        }

        .cs-dropdown-item {
            background-color: $t-foreground;
        }
        .cs-dropdown-item:hover {
            background-color: $dropdown-hover;
        }
    }

